<template>
  <div>
      <div class="input">
          <el-row>
              <el-col>
                  <div style="font-size:12px;color:#6d757a;text-align:left;margin-bottom:5px">
                      <span>{{ob.id}}</span>
                  </div>
              </el-col>

              <el-col v-if="ob.titl!='img'">
                  <div style="font-size:14px;color:#6d757a;text-align:left;margin-bottom:5px">
                      <span>{{ob.titl}}</span>
                  </div>
              </el-col>

              <el-col v-if="ob.titl!='img'">
                  <div style="font-size:14px;text-align:left;margin-bottom:15px">
                      <span>{{ob.deta}}</span>
                  </div>
              </el-col>
              <el-col v-else>
                  <div style="font-size:14px;text-align:left;margin-bottom:15px">
                      <span>
                          <img v-bind:src='this.url'/>
                      </span>
                  </div>
              </el-col>


            <el-col style="font-size:12px;color:#6d757a;text-align:left;">

                <el-button size="mini" icon="el-icon-caret-top" circle @click="thumb"></el-button> <span>{{ob.up_num}}</span>
                <el-button size="mini" icon="el-icon-caret-bottom" circle @click="diss"></el-button> <span>{{ob.down_num}}</span>
                <el-button size="mini" icon="el-icon-warning-outline" circle @click="open"></el-button>
                <el-divider></el-divider>
            </el-col>

          </el-row>


      </div>
  </div>
</template>

<script>
export default {
    name:'pld',
    data()
    {
        return{
            url:'',
            urid:'',
                id:'用户的id',
                deta:'具体的内容',
                titl:'题目',
                up_num:999,
                down_num:999,

            thum:true,
            down:true,
           }
    },
    props:{
        ob:{
            id:'',
            deta:'',
            titl:'',
            up_num:999,
            down_num:999,
        },
        nowID:{type:Number,default:0},
    },
    methods:
    {
        /*还差一个函数，页面关闭的时候再把thumb这些数据传回去*/
        upt(){
            var ist=0;
            if(!this.thum) ist=1;
            if(this.urid=='') alert("请先登录！");
            else {
                let params={hpno:this.nowID,id:this.ob.id,titl:this.ob.titl,deta:this.ob.deta,username:this.urid,ist:ist};
                this.$http.post('/hpt.php',params).then(
                    response=>{
                        console.log(response)
                    }
                )
            }
        },
        upd(){
            var isd=0;
            if(!this.down) isd=1;
            if(this.urid=='') alert("请先登录！");
            else {
                let params={hpno:this.nowID,id:this.ob.id,titl:this.ob.titl,deta:this.ob.deta,username:this.urid,isd:isd};
                this.$http.post('/hpd.php',params).then(
                    response=>{
                        console.log(response)
                    }
                )
            }
        },
        gettd(){
            let params={hpno:this.nowID,id:this.ob.id,titl:this.ob.titl,deta:this.ob.deta};
            this.$http.post('/gettd.php',params).then(
                response=>{
                    //console.log(response);
                    var ss=response.data;
                    if(ss!=null){
                        this.ob.up_num=ss[0];
                        this.ob.down_num=ss[1];
                    }
                }
            )
        },
        thumb()
        {
            if(this.thum)
            {
                this.ob.up_num++;
                this.thum=!this.thum;
            }
            else
            {
                this.ob.up_num--;
                this.thum=!this.thum;
            }
            this.upt();
        },
        diss()
        {
            if(this.down)
            {
                this.ob.down_num++;
                this.down=!this.down;
            }
            else
            {
                this.ob.down_num--;
                this.down=!this.down;
            }
            this.upd();
        },
          open() {
                this.$prompt('请输入举报原因', '举报', {
                confirmButtonText: '确定',
                cancelButtonText: '取消',

                inputPattern: /^[\s\S]*.*[^\s][\s\S]*$/,
                inputErrorMessage: '举报信息不能为空！'
                }).then(
                ({value}) =>
                {
                    // this.$message({
                    //     type: 'success',
                    //     message: '你的邮箱是: ' + value
                    // });
                    this.$message({
                    type: 'info',
                    message: '举报成功'
                });

                let params={deta:value};
                    this.$http.post('/JUBAO.php',params).then(
                    response=>{
                        console.log(response)
                    }
                    )

                }).catch(() => {
                this.$message({
                    type: 'info',
                    message: '取消举报'
                });
                });
            },
    },
    created() {
        var guro=localStorage.getItem('username');
        var gurt=sessionStorage.getItem('username');
        if(guro!=null) this.urid=guro;
        if(gurt!=null) this.urid=gurt;
        if(this.ob.titl=='img'){
            this.url=require('@/static/'+this.ob.deta);
        }
        this.gettd();
    },
    updated() {
        this.gettd();
    }
}
</script>

<style scoped>

.grid-content {
    border-radius: 4px;
    min-height: 36px;
  }



</style>
