<template>
  <div>
    <el-row>
      <el-col :span="24" class="grid-content bg_douban">
        <div class="upup">
          <span style="float:left;margin-left:20px;" @click="jmp('/')">礼朴</span>
          <span style="float:left;margin-left:20px;" @click="jmp('/book')">读书</span>
          <span style="float:left;margin-left:20px;" @click="jmp('/movie')">电影</span>
          <span style="float:left;margin-left:20px;" @click="jmp('/group')">小组</span>
          <span style="float:right;margin-right:20px;">了解更多</span>
          <span style="float:right;margin-right:20px;" @click="jmp('/regi')">登录/注册</span>
        </div>
      </el-col>
    </el-row>

    <el-row style="background:#e3ebec;">
      <el-row style="margin-top:15px;margin-bottom:20px;">
        <el-col :span="4">
          <div class="grid-content"></div>
        </el-col>
        <el-col :span="4">
          <el-image
                  style="height:30%;width:30%"
                  :src="'http://r.photo.store.qq.com/psc?/V12aZVkM2Rfh0C/YWvjNfAyIVey1fwA2tD8GFgFCZvt4zguqnPtEs9uY7sQOqZLv07LPCGAwcCrGmgl3w4Ih3E9ou2ZBkE4UeXK08mSRqGkByGaxeM7ZkovuYQ!/r'"></el-image>
        </el-col>
        <el-col :span="8">
          <el-input
            suffix-icon="el-icon-search"
            placeholder="搜索感兴趣的话题"
            v-model="keyword"
            v-on:click.native="search"
          ></el-input>
        </el-col>
      </el-row>
      <el-divider></el-divider>
    </el-row>

    <el-row>
      <el-col :span="15">
        <el-col :span="7">
          <div class="grid-content"></div>
        </el-col>

        <el-col :span="17">

          <el-card :body-style="{ padding: '0px' }">
            <div style="padding: 20px;">

              <div style="font-size:26px;margin-bottom:10px;color:#333333;font-weight:bold;text-align:left">
                <span>
                    {{hd.titl}}
                </span>
              </div>

              <div style="font-size:14px;margin-bottom:10px;">
                <div style="font-size:14px;margin-bottom:10px;color:#333333;text-align:left">
                <span >
                    {{hd.de}}
                </span>
                <el-divider>
                </el-divider>
                </div>
<!-- 这里是上传图片的部分！！！！！！！！！！！！！！！！！！！！！！！！！！！！！！！！！！！！！！！！！！！！！！！！！ -->
                <el-col>
                    <el-form :model="form">
                        <el-form-item  >
                          <el-upload
                            ref="upload"
                            action="#"
                            accept="image/png,image/gif,image/jpg,image/jpeg"
                            list-type="picture-card"
                            :limit=limitNum
                            :auto-upload="false"
                            :on-exceed="handleExceed"
                            :before-upload="handleBeforeUpload"
                            :on-preview="handlePictureCardPreview"
                            :on-remove="handleRemove">
                            <i class="el-icon-plus"></i>
                          </el-upload>
                          <el-dialog :visible.sync="dialogVisible">
                            <img width="100%" :src="dialogImageUrl" alt="">
                          </el-dialog>
                        </el-form-item>
                        <el-form-item>
                          <el-button size="small" type="primary" @click="uploadFile">立即上传</el-button>
                          <el-button size="small">取消</el-button>
                        </el-form-item>
                      </el-form>
                </el-col>
<!-- 这里是上传图片的部分！！！！！！！！！！！！！！！！！！！！！！！！！！！！！！！！！！！！！！！！！！！！！！！！！ -->
              </div>
            </div>
          </el-card>

            <el-col style="margin-top:30px;">
                <comment>
                </comment>
            </el-col>

            <!-- <el-col>
              这里就从后端拿所有的文字评论出来
            </el-col> -->
            <el-col v-for="(o,i) in lists.slice(0,hpn)" :key="i">
              <pld v-bind:ob=o v-bind:nowID=hpno />
            </el-col>

        </el-col>
      </el-col>
      <el-col :span="9">
        <el-col :span="14">
          <div class="hua">
            话题广场体现礼朴论坛的话题趋势。话题是有主题的内容聚合，是礼朴er的沙龙，你可以在此发现同一主题下的精彩内容，更欢迎你写新动态、添加已有动态、发#话题#广播来参与讨论。
            话题广场即将开放发起话题、邀请参与等功能。
          </div>
        </el-col>

        <el-col :span="10">
          <div class="grid-content"></div>
        </el-col>
      </el-col>
    </el-row>
  </div>
</template>

<script>
import comment from '@/components/comment.vue'
import pld from '@/components/pld.vue'
export default {
  name: "hotspot_detail",
  components:{
        comment,
        pld,
},
  data() {
    return {
      file1:'',
      lists:[
        {id:'cnm',titl:'cnmb',deta:"cnll"},
        {id:'cnm',titl:'cnmb',deta:"cnll"},
        {id:'cnm',titl:'cnmb',deta:"cnll"},
      ],
      st_lists:[],
      keyword: "",
      hd:{
          titl:'我是标题',
          de:'我是详情'
      },
      dialogImageUrl: '',
      dialogVisible: false,
      formLabelWidth: '80px',
      limitNum: 1,
      form: {},

      list2:
      {
        id:'我是用户的id',
        deta:'我是用户写的废话',
        titl:'我是用户起的标题',
        up_num:1,
        down_num:10
      },
      hpn:0,
      hpno:0
    };
  },
  methods: {
    jmp(addr){
       this.$router.push({path:addr});
    },
    upImg(file){
      let reader=new FileReader();
      let img=new Image();
      var base;
      reader.readAsDataURL(file);
      reader.onloadend=(e)=>{
        img.src=e.target.result
        base=reader.result;
        //console.log(base);
        let params={hpno:this.hpno,id:this.urid,titl:'img',base:base};
        this.$http.post('/base64.php',params).then(
                response=>{
                  console.log(response);
                }
        )
      }
      //console.log(base);
    },
    search() {
      console.log(this.keyword);
       this.$router.push({path:'/h'});
    },
     // 上传文件之前的钩子
    handleBeforeUpload(file){
      this.upImg(file)
      console.log('before')
      if(!(file.type === 'image/png' || file.type === 'image/gif' || file.type === 'image/jpg' || file.type === 'image/jpeg')) {
        this.$notify.warning({
          title: '警告',
          message: '请上传格式为image/png, image/gif, image/jpg, image/jpeg的图片'
        })
      }
      let size = file.size / 1024 / 1024 / 2
      if(size > 2) {
        this.$notify.warning({
          title: '警告',
          message: '图片大小必须小于2M'
        })
      }
    },
    // 文件超出个数限制时的钩子
    handleExceed(files, fileList) {
      console.log(files, fileList);
      this.$notify.warning({
          title: '警告',
          message: '文件数不能超过一个'
        })
    },
    // 文件列表移除文件时的钩子
    handleRemove(file, fileList) {
      console.log(file, fileList);
    },
    // 点击文件列表中已上传的文件时的钩子
    handlePictureCardPreview(file) {
      this.dialogImageUrl = file.url;
      this.dialogVisible = true;
    },
    uploadFile() {
      this.$refs.upload.submit()
      //console.log(file)
    }


  },
  created() {
    this.hpno=this.$route.query.hpno;
    //alert(this.hpno);
    console.log(this.hpno);
    var guro=localStorage.getItem('username');
    var gurt=sessionStorage.getItem('username');
    if(guro!=null) this.urid=guro;
    if(gurt!=null) this.urid=gurt;
    console.log(guro);
    console.log(gurt);
    console.log(this.urid);
    let params={op:2,hpno:this.hpno};
    this.$http.post('/gethp.php',params).then(
            response=>{
              this.lists=[];
              var pllist=response.data
              //console.log(pllist)
              this.hpn=pllist.length
              var i;
              for(i=0;i<pllist.length;i++){
                var s1=pllist[i][0];
                var s2=pllist[i][1];
                var s3=pllist[i][2];
                var s4=pllist[i][3];
                var s5=pllist[i][4];
                var tmp={id:s1,titl:s2,deta:s3,up_num:s4,down_num:s5};
                this.lists.push(tmp);
              }
              this.st_lists=this.lists;
            }
    )
    let paramst={op:4,hpno:this.hpno};
    this.$http.post('/gethp.php',paramst).then(
            response=>{
              //console.log(response);
              var ss=response.data;
              this.hd.titl=ss[0];
              this.hd.de=ss[1];
            }
    )
  }
};
</script>

<style scoped>
body {
  margin: 0;
  padding: 0;
}
.el-divider--horizontal {
  margin: 5px;
}
.el-col {
  border-radius: 4px;
}
.bg-purple-dark {
  background: #99a9bf;
}
.bg_douban {
  background: #545652;
}
.bg-purple {
  background: #d3dce6;
}
.bg-purple-light {
  background: #e5e9f2;
}
.grid-content {
  border-radius: 4px;
  min-height: 36px;
}
.row-bg {
  padding: 10px 0;
  background-color: #f9fafc;
}
.upup {
  color: #d5d5d5;
  font-size: 13px;
  margin-top: 6px;
}

.hua {
  margin-top: 45px;
  margin-bottom: 75px;
  margin-left: 30px;
  text-align: left;
  color: #999999;
  font-size: 13px;
}
</style>
